/* logo styles */

.logo {
  font-size: 24px;
  line-height: 1;

  @include media("<desktop") {
    font-size: 22px;
  }

  @include media("<phone") {
    font-size: 20px;
  }

  > a {
    display: block;
    color: $black;
    padding: 38px 0;

    @include media("<desktop") {
      padding: 30px 0;
    }

    @include media("<tablet") {
      padding: 25px 0;
    }

    @include media("<phone") {
      padding: 20px 0;
    }
  }

  .logo-mark {
    font-weight: 800;
    color: $red;
    background: transparent;
  }
}
